import { MapperInterface } from '@telenet/ng-lib-shared';
import { Injectable } from '@angular/core';
import { RawEasySwitchDetailsInterface, RawEasySwitchInterface } from '../interfaces/raw-easy-switch-id.interface';
import { EasySwitchDetail } from '../models/easy-switch-detail.model';

@Injectable({
  providedIn: 'root',
})
export class EasySwitchDetailMapper implements MapperInterface<RawEasySwitchInterface, EasySwitchDetail[]> {
  toModel(rawEasySwitchDetails: RawEasySwitchInterface): EasySwitchDetail[] {
    const easySwitchDetails = [];
    rawEasySwitchDetails.easySwitchIds.forEach((rawEasySwitchDetail) => {
      easySwitchDetails.push(this.createEasySwitchDetails(rawEasySwitchDetail));
    });
    return easySwitchDetails;
  }

  private createEasySwitchDetails(rawEasySwitchDetail: RawEasySwitchDetailsInterface): EasySwitchDetail {
    const easySwitchDetail = new EasySwitchDetail();
    easySwitchDetail.id = rawEasySwitchDetail.id;
    easySwitchDetail.addressId = rawEasySwitchDetail.addressId;
    return easySwitchDetail;
  }
}
