import isEmpty from 'lodash-es/isEmpty';
import { CustomerConstants } from '../constants/customer.constant';
import every from 'lodash-es/every';
import { RawCommercialAddress, RawCustomerLocationInterface, RawProduct } from '../interfaces/customer.interface';
import { ServiceSegmentEnum } from '../enums/service-segment.enum';

export class Customer {
  readonly isSuccess = true;
  id: string;
  status: string;
  category: string;
  brand: string;
  products: RawProduct[];
  legalAddress: RawCommercialAddress;
  commercialAddress: RawCommercialAddress;
  companyName: string;
  legalForm: string;
  belgianVATNumber: string;
  foreignVATNumber: string;
  belgianEnterpriseNumber: string;
  firstName: string;
  lastName: string;
  accountNumber: string;
  name: string;
  placeOfBirth: string;
  cardIdNumber: string;
  rrn: string;
  customerLocations: RawCustomerLocationInterface[];
  serviceSegment: ServiceSegmentEnum;
  role: string;

  isBusiness(): boolean {
    return this.category && this.category.toLowerCase() === CustomerConstants.CATEGORY_BUSINESS;
  }

  isResidential(): boolean {
    return this.category && this.category.toLowerCase() === CustomerConstants.CATEGORY_RESIDENTIAL;
  }

  isOfCategory(customerCategory: string): boolean {
    return this.category && this.category.toLowerCase() === customerCategory.toLowerCase();
  }

  getVatNumber(): string {
    if (this.belgianVATNumber) {
      return this.belgianVATNumber;
    } else if (this.foreignVATNumber) {
      return this.foreignVATNumber;
    } else if (this.belgianEnterpriseNumber) {
      return this.belgianEnterpriseNumber;
    }
  }

  hasOnlyProductsByType(planType: string): boolean {
    return (
      !isEmpty(this.products) &&
      every(this.products, function (product) {
        return planType.toLowerCase() === product.planType.toLowerCase();
      })
    );
  }

  isValid(): boolean {
    return !isEmpty(this.category);
  }
}
